import WebIM from '../utils/WebIM';
// import cookie from "vue-cookies";
import { toast } from "vant";
const Login = {
    state: {
        userId: -1,
        password: "",
        isLogin: false,
        token: "",
    },
    mutations: {
        // 将页面参数赋给全局模块Login参数
        setByUserInfo(state, option) {
            state.userId = option.userId,
                state.password = option.password,
                // 其他页面重新刷新时，数据会丢失，可以从sesstionStorge中获取，
                sessionStorage.setItem("wxUserInfo", JSON.stringify(option))
        },
        // 是否登录成功
        setIsLogin(state, boo) {
            state.isLogin = boo
        },

    },
    actions: {
        // 将登录页面的信息传入, 通过以下WebIM验证才可以进行下一步
        isByVerification(context, payload) {
            WebIM.conn
                .open({
                    user: payload.userId,
                    accessToken: payload.token,
                })
                .then(() => {
                    // 登录成功同时注册一个本地的token
                    localStorage.setItem("wxUserToken", payload.token, 0)
                    context.commit('setIsLogin', true)
                    // 将信息传到全局模块中,修改全局模块Login的参数 
                    context.commit("setByUserInfo", payload)
                   
                })
        }
    }
}
export default Login;