const groupChat = {
    state: {
        // 我发送的信息
        textMessagesArr: [],
        // 别人发送的信息
        otherPeopleTextMessagesArr:[]
    },
    mutations: {
        setTextMessagesArr(state, text) {
            state.textMessagesArr.push(text)
        },
        setOtherPeopleTextMessagesArr(state,obj){
            state.otherPeopleTextMessagesArr.push(obj)
            console.log(123);
        }
    },
    actions: {

    }
}

export default groupChat