
const User = {
    state: {
        // 用户id
        userId: "",
        // 用户头像
        userPic: "",
        // 用户昵称
        nickName: "用户",
        // 创建新群聊时, 各成员接受一个参数渲染一个群组在 "homeView' 页面,
        // groupAcceptNews: [],
    },
    mutations: {
        setAppUserIdM(state, id) {
            state.userId = id
        },
        setUserInfo(state, obj) {
            state.userId = obj.phone
            state.userPic = obj.avatarurl
            state.nickName = obj.nickname
        },
    },
    actions: {
        setAppUserIdA(context, payload) {
            context.commite("setAppUserIdM", payload)
        },
        setAppUserInfo(context, payload) {
            context.commite("setUserInfo", payload)
        }
    }
}
export default User;