import vue from '@/main';
import WebIM from '../utils/WebIM';

const Home = {
    state: {
        newCreateGroupId: "",
        // 创建新群聊时, 各成员接受一个参数渲染一个群组在 "homeView' 页面,
        GroupAcceptNews: false,

        // 点击homeView 时,临时存储群聊id,主要用于群聊页面,群聊详情页面
        cacheClickGroupId: "",

        // 是否有更新的群公告
        isUpdateGourpAnnouncement: false,

        // 更新的群公告的群ID
        updateGourpAnnouncementId: ""
    },
    mutations: {
        setNewCreateGroupId(state, obj) {
            state.newCreateGroupId = obj.id
            state.GroupAcceptNews = obj.isNewCreate
        },
        setGroupAcceptNews(state, boo) {
            state.GroupAcceptNews = boo
        },
        setCacheClickGroupId(state, id) {
            state.cacheClickGroupId = id
        },
        setIsUpdateGourpAnnouncement(state, obj) {
            sessionStorage.setItem("isUpdateGourpAnnouncement", obj.boo)
            sessionStorage.setItem("updateGourpAnnouncementId", obj.updateGroupId)
            state.isUpdateGourpAnnouncement = obj.boo
            state.updateGourpAnnouncementId = obj.updateGroupId
        }
    },

    actions: {
        setCreateGroupAcceptNews(context, payload) {
            if (payload !== "") {
                context.commit("setNewCreateGroupId", payload)
                sessionStorage.setItem("newCreateGroupId", payload.id)
            }
        }
    }
}

export default Home;