<template>
  <div class="data-setting">
    <nav>
      <van-row>
        <van-col span="4" @click="backFunc">&lt;</van-col>
        <van-col span="14" offset="1">资料设置</van-col>
      </van-row>
    </nav>
    <main>
      <div class="main-center">
        <div class="list between">
          <span>设置备注和标签</span>
          <span>
            <strong>{{ nickname }}</strong> &gt;</span
          >
        </div>
        <div class="list between">
          <span>朋友权限</span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
      </div>
      <div class="main-center">
        <div class="list between">
          <span>把他推荐给朋友</span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
        <div class="list between">
          <span>添加到桌面</span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
      </div>
      <div class="main-center">
        <div class="list between">
          <span>设为星标朋友</span>
          <a-switch v-model="checked1" @change="onChange1" />
        </div>
      </div>
      <div class="main-center">
        <div class="list between">
          <span>加入黑名单</span>
          <a-switch v-model="checked2" @change="onChange2" />
        </div>
        <van-popup v-model="isPopup">
          <div class="popup-top">
            <h3>加入黑名单</h3>
            <p>
              加入黑名单，你将不再收到对方的消息，并且你们相互看不到对方朋友圈的更新
            </p>
          </div>
          <div class="popup-bottom">
            <div class="no" @click="cancelFunc()">取消</div>
            <div class="yes" @click="addBlackFriend(phone)">确定</div>
          </div>
        </van-popup>
        <div class="list between">
          <span>投诉</span>
          <van-icon name="arrow" color="rgb(107, 107, 107)" />
        </div>
      </div>
      <div class="main-bottom">
        <div class="bottom-item" @click="delFriend()">删 除</div>
      </div>
      <van-popup v-model="isDelFriends">
        <div class="popup-top">
          <h3>删除联系人</h3>
          <p>将联系人 {{ nickname }} 删除，将同时删除与该联系人的聊天记录</p>
        </div>
        <div class="popup-bottom">
          <div class="no" @click="cancelDelFriend()">取消</div>
          <div class="del" @click="confirmDelFriend(phone)">删除</div>
        </div>
      </van-popup>
    </main>
  </div>
</template>

<script>
import { Toast } from "vant";
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      value: true,
      checked1: false,
      // 是否拉入黑名单
      checked2: false,
      // 弹出框
      isPopup: false,
      // 是否删除朋友
      isDelFriends: false,
      // 数组长度
      length: 0,
    };
  },
  created() {
    this.getBlackFriendsList();
  },
  computed: {
    nickname() {
      return JSON.parse(this.$route.params.list).nickname;
    },
    phone() {
      return JSON.parse(this.$route.params.list).phone;
    },
    ...mapState({
      userId: (state) => state.user.userId,
      blackFriendsBasic: (state) => state.friends.blackFriendsBasic,
    }),
  },
  watch: {
    userId() {
      this.getBlackFriendsList();
    },
    blackFriendsBasic(newVal) {
      this.length = newVal.length;
      this.showIsBlack();
    },
    checked2(newVal) {
      this.checked2 = newVal;
    },
  },
  methods: {
    ...mapActions([
      "delFridends",
      "addBlackFriends",
      "cancelBlackFriends",
      "getBlackFriendsList",
    ]),

    backFunc() {
      this.$router.push({
        name: "home",
      });
    },
    onChange1(checked) {
      console.log(`a-switch to ${checked}`);
    },
    onChange2(checked) {
      console.log(`a-switch to ${checked}`);
      this.isPopup = checked;

      // 移出黑名单
      if (!checked) {
        Toast.success("已移出黑名单");
        this.cancelBlackFriends(this.phone);
        setTimeout(() => {
          this.$router.push({
            name: "home",
          });
        }, 1000);
      }
    },
    // 若已是黑名单朋友则按钮仍为true
    showIsBlack() {
      for (let i = 0; i < this.length; i++) {
        if (this.blackFriendsBasic[i].phone === this.phone) {
          this.checked2 = this.blackFriendsBasic[i].isBlack;
        }
      }
    },
    // 取消加入黑名单
    cancelFunc() {
      this.isPopup = false;
      this.checked2 = false;
    },
    // 加入黑名单
    addBlackFriend(id) {
      this.addBlackFriends(id);
      Toast.success("已移入黑名单");

      setTimeout(() => {
        this.$router.push({
          name: "home",
        });
      }, 1000);
    },
    // 删除朋友
    delFriend() {
      this.isDelFriends = true;
    },
    // 确认删除好友
    confirmDelFriend(id) {
      this.delFridends(id);
      
      Toast.success("删除成功");
      this.$router.push({
        name: "home",
      });
    },
    cancelDelFriend() {
      this.isDelFriends = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.data-setting {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: rgb(17, 17, 17);
  color: white;

  nav {
    width: 100vw;
    height: 45px;
    background-color: rgb(17, 17, 17);
    line-height: 45px;
    color: white;
    text-align: center;
    font-size: 14px;
  }

  .main-center {
    margin-bottom: 10px;
  }

  .main-bottom {
    text-align: center;
    line-height: 40px;

    .bottom-item {
      background-color: rgb(24, 24, 24);
      color: rgb(255, 94, 0);
      border: 1px solid rgba(204, 203, 203, 0.1);
    }
  }

  .list {
    line-height: 25px;
    display: flex;
    flex-direction: row;
    padding: 15px;
    border-bottom: 1px solid rgba(204, 203, 203, 0.1);
    background-color: rgb(24, 24, 24);

    &.between {
      justify-content: space-between;
    }

    strong {
      color: gray;
      margin-right: 5px;
    }
  }

  .ant-switch {
    background-color: rgb(50, 50, 50);
  }

  .ant-switch-checked {
    background-color: rgb(5, 193, 97);
  }

  .van-popup {
    height: 200px;
    width: 85%;
    border-radius: 10px;
    background-color: rgb(35, 35, 35);

    .popup-top {
      padding: 20px;
      line-height: 25px;
      color: whitesmoke;

      h3 {
        text-align: center;
        font-size: 14px;
        margin: 5px 0 10px !important;
      }
    }
    .popup-bottom {
      display: flex;
      flex-direction: row;
      text-align: center;
      margin-top: 18px;
      border-top: 1px solid rgba(204, 203, 203, 0.1);

      div {
        width: 50%;
        height: 50px;
        line-height: 50px;

        &:nth-of-type(1) {
          border-right: 1px solid rgba(204, 203, 203, 0.1);
        }
      }

      .yes {
        color: rgb(160, 196, 247);
      }

      .del {
        color: rgb(255, 94, 0);
      }
    }
  }
}
</style>