<template>
  <div class="home">
    <nav>
      <h2>主页</h2>
      <div class="address-icon">
        <SearchIcon />
        <AddComponent />
      </div>
    </nav>
    <div class="home-list">
      <!-- v-show="allChatList.groupId !== undefined" -->
      <div
        v-show="newCreteGroup !== ''"
        class="create-grop-item"
        @click="routerGroupChatPage(newCreteGroup.newGroupId)"
      >
        <img src="../assets/image/images/groupImg.webp" />
        <span
          v-if="
            isUpdateGourpAnnouncement == true &&
            newCreteGroup.newGroupId == updateGourpAnnouncementId
          "
          class="dot"
        ></span>
        <span>{{ newCreteGroup.newGroupName }}</span>
      </div>
      <div
        class="create-grop-item"
        @click="routerGroupChatPage(item.data.groupId)"
        v-for="(item, i) in groupList"
        v-show="isGetGroupList"
        :key="i"
      >
        <img src="../assets/image/images/groupImg.webp" />
        <span
          v-if="
            isUpdateGourpAnnouncement == true &&
            item.data.groupId == updateGourpAnnouncementId
          "
          class="dot"
        ></span>
        <span>{{ item.data.groupName }}</span>
        <!-- <span>{{ item.timestamp }}</span> -->
      </div>

      <div
        class="create-grop-item"
        @click="routerOntChatPage(item.phone, item.nickname)"
        v-for="item in renderList"
        v-show="isGetOneList"
        :key="item.phone"
      >
        <img v-if="item.avatarurl !== undefined" :src="item.avatarurl" />
        <img
          v-if="item.avatarurl == undefined"
          src="../assets/image/images/default_hd_avatar.png"
        />
        <van-badge
          class="mes-dot"
          v-show="getCount(item.phone) != 0"
          :content="getCount(item.phone)"
          max="99"
        ></van-badge>
        <div class="desc">
          <span>{{ item.nickname }}</span>
          <span class="text">{{ getMesg(item.phone) }}</span>
        </div>

        <!-- <span>{{ item.timestamp }}</span> -->
      </div>
    </div>
  </div>
</template>

<script>
import AddComponent from "../components/AddComponent.vue";
import SearchIcon from "../components/SearchIcon.vue";
import { mapState, mapMutations, mapActions } from "vuex";
import WebIM from "../utils/WebIM";

export default {
  name: "HomeView",
  data() {
    return {
      // 单聊列表
      oneToOneList: [],
      // 单聊列表是否获取完毕
      isGetOneList: false,
      // 群组列表
      groupList: [],
      // 群聊列表是否获取完毕
      isGetGroupList: false,
      // 单聊和群聊的信息获取完毕
      isGetAllDataList: false,
      // 数据全部获取成功
      isGetAllDataFormatList: false,
      // 单聊+群聊(按照时间创建)列表
      /* 
      allChatList:[
        timestamp: 时间戳
        data: {
          朋友信息 / 群聊信息
          朋友信息:avatarurl,ext,gender,nickname,phone
          群聊信息:groupId,groupName
        }
      ]
       */
      allChatList: [],
      newCreteGroup: "",
      blackList: [],
      length: 0,
      isRender: false,
    };
  },
  components: {
    AddComponent,
    SearchIcon,
  },
  computed: {
    ...mapState({
      userPhone: (state) => state.user.userId,
      GroupAcceptNews: (state) => state.home.GroupAcceptNews,
      newCreateGroupId: (state) => state.home.newCreateGroupId,

      blackFriendsList: (state) => state.friends.blackFriendsList,
      isRenderBlackFriends: (state) => state.friends.isRenderBlackFriends,
      isRenderSelectFriends: (state) => state.friends.isRenderSelectFriends,
      renderList: (state) => state.friends.renderList,
      friendsAllBasicArr: (state) => state.friends.friendsAllBasicArr,
      messageCountList: (state) => state.chat.messageCountList,
      isUpdateGourpAnnouncement: (state) =>
        state.home.isUpdateGourpAnnouncement,
      updateGourpAnnouncementId: (state) =>
        state.home.updateGourpAnnouncementId,
    }),
  },
  created() {
    setTimeout(() => {
      this.getBlackFriendsList();
      this.delAllFirendBasic();
      this.searchAllFriends();
      this.renderFriendsList();
    }, 100);

    this.getAllListData();
    this.setGroupAcceptNews(false);
    if (this.allChatList == []) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
      });
    } else {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: false,
      });
    }
  },

  watch: {
    userPhone() {
      this.getBlackFriendsList();
      this.delAllFirendBasic();
      this.searchAllFriends();
      this.renderFriendsList();
      this.getAllListData();
      let boo = JSON.parse(sessionStorage.getItem("isUpdateGourpAnnouncement"));
      let obj = {
        updateGroupId: JSON.parse(
          sessionStorage.getItem("updateGourpAnnouncementId")
        ),
        boo: boo,
      };
      this.$store.commit("setIsUpdateGourpAnnouncement", obj);
    },
    isRenderBlackFriends() {
      if (this.isRenderBlackFriends && this.isRenderSelectFriends) {
        this.isRender = true;
      }
    },
    isRenderSelectFriends() {
      if (this.isRenderBlackFriends && this.isRenderSelectFriends) {
        this.isRender = true;
      }
    },
    isRender(newVal) {
      if (newVal) {
        this.renderFriendsList();
      }
    },
    GroupAcceptNews(newV) {
      if (newV) {
        let option = {
          // 群组 ID
          groupId: this.newCreateGroupId,
        };
        WebIM.conn.getGroupInfo(option).then((r) => {
          this.newCreteGroup = {
            newGroupName: r.data[0].name,
            newGroupId: r.data[0].id,
          };
        });
      }
    },
  },
  methods: {
    ...mapMutations(["setGroupAcceptNews", "renderFriendsList"]),
    ...mapActions([
      "getBlackFriendsList",
      "searchAllFriends",
      "delAllFirendBasic",
    ]),

    getCount(id) {
      if (this.userPhone) {
        if (this.messageCountList != 0) {
          let count = "";
          this.messageCountList.forEach((item) => {
            if (item.from == id) {
              count = item.count;
            }
          });
          return count;
        }
      }
    },
    getMesg(id) {
      if (this.userPhone) {
        if (this.messageCountList != 0) {
          let text = "";
          this.messageCountList.forEach((item) => {
            if (item.from == id) {
              text = item.text;
            }
          });
          return text;
        }
      }
    },

    getAllListData() {
      if (this.userPhone !== "") {
        this.getOneToOneListData();
        this.getGroupListData();
      }
    },
    getOneToOneListData() {
      WebIM.conn.getContacts().then((res) => {
        res.data.map((item, i) => {
          WebIM.conn.fetchUserInfoById(item).then((r) => {
            this.oneToOneList.push(r.data[item]);
            if (i == res.data.length - 1) {
              this.isGetOneList = true;
            }
          });
        });
      });
    },
    getGroupListData() {
      WebIM.conn
        .getJoinedGroups({
          pageNum: 1,
          pageSize: 20,
          needAffiliations: false,
          needRole: false,
        })
        .then((res) => {
          res.data.map((item, j) => {
            let option = {
              // 群组 ID 或群组 ID 数组。
              groupId: item.groupid,
            };
            WebIM.conn.getGroupInfo(option).then((r) => {
              r.data[0].affiliations.map((i) => {
                if (i.owner == this.userPhone || i.member == this.userPhone) {
                  this.groupList.push({
                    timestamp: i.joined_time,
                    data: {
                      groupId: item.groupid,
                      groupName: item.groupname,
                    },
                  });
                  if (j == res.data.length - 1) {
                    this.isGetGroupList = true;
                  }
                }
              });
            });
          });
        });
    },
    routerGroupChatPage(groupId) {
      sessionStorage.setItem("groupInfoCacheSave", groupId);
      this.$store.commit("setCacheClickGroupId", groupId);
      this.$router.push({
        name: "newGroupChatPage",
        params: {
          groupId,
        },
      });
    },
    routerOntChatPage(phone, friendNickName) {
      this.$router.push({
        path: "/chatdes",
        query: {
          phone,
          friendNickName,
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.home {
  nav {
    position: relative;
    height: 45px;
    background-color: rgb(17, 17, 17);
    color: white;
    text-align: center;
    line-height: 45px;

    .address-icon {
      width: 60px;
      position: absolute;
      right: 15px;
      top: 10px;
      display: flex;
      justify-content: space-between;
    }
  }

  .home-list {
    color: rgb(164, 164, 164);
    width: 100%;
    height: 88vh;
    background-color: rgb(20, 20, 20);

    .create-grop-item {
      position: relative;
      background-color: rgb(24, 24, 24);
      padding: 8px 15px;
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 50px;
     

      .dot {
        position: absolute;
        top: 7px;
        left: 40px;
        width: 8px;
        height: 8px;
        border-radius: 999px;
        background-color: red;
        border: 1px solid #fff;
      }

      .mes-dot {
        position: absolute;
        left: 50px;
        top: 5px;
        border: none;
        z-index: 6;
      }

      img {
        width: 40px;
        height: 40px;
        border-radius: 5px;
      }
      span {
        margin-left: 10px;
      }

      .desc {
        display: flex;
        flex-direction: column;

        .text {
          font-size: 12px;
        }
      }
    }
  }
}
</style>