<template>
  <div class="chatdes">
    <nav>
      <div class="left" @click="back">&lt;</div>
      <div class="friendName">{{ username }}</div>
      <div class="about">...</div>
    </nav>
    <main @click="handle" style="color: #fff">
      <div class="chatbox" v-for="(item, index) in megList" :key="index">
        <div :class="[item.to == phone ? 'right' : 'left']">
          <!-- 用户头像 -->
          <span class="img">
            <img v-if="item.to == phone" :src="imgUrl1" alt="" />
            <img v-else :src="imgUrl2" alt="" />
          </span>
          <!-- 文本 -->
          <div
            class="text"
            v-if="item.contentsType == 'TEXT' || item.type == 'txt'"
          >
            <span>{{ item.msg || item.data }}</span>
          </div>
          <!-- 照片  -->
          <div
            class="photo"
            v-else-if="item.contentsType == 'IMAGE' || item.type == 'img'"
          >
            <!-- 这里判断是否是img -->
            <img :src="item.url" alt="" />
          </div>
          <div
            class="file"
            v-else-if="item.contentsType == 'FILE' || item.type == 'file'"
          >
            <span class="name">{{ item.filename }}</span>
            <div class="bottom">
              <div class="length">{{ item.file_length }}</div>
              <a :href="item.url" download="下载名称" style="color: #666"
                >点击下载</a
              >
            </div>
          </div>
        </div>
      </div>
    </main>
    <footer>
      <div class="choose">
        <div class="left">
          <img src="@/assets/image/ChatDesView/voice.png" />
        </div>
        <div class="container" @click="(emojiShow = false), (show = false)">
          <input v-model="message" type="text" />
        </div>
        <div class="right">
          <span @click="emoShow">
            <img src="@/assets/image/ChatDesView/emote.png" />
          </span>
          <span @click="handleClick" v-if="!message">
            <img src="@/assets/image/ChatDesView/more.png" />
          </span>
          <button
            v-else
            @click="onSendTextMsg('txt'), (emojiShow = false), (show = false)"
          >
            发送
          </button>
        </div>
      </div>
      <transition name="slide">
        <div class="list" v-show="show">
          <div class="item" @click="clickFile">
            <input type="file" ref="imgDom" @change="pictureChange" />
            <img src="@/assets/image/ChatDesView/file.png" />
            <span>文件</span>
          </div>
          <div class="item" @click="takePhoto">
            <img src="@/assets/image/ChatDesView/takephoto.png" />
            <span>拍摄</span>
          </div>
          <div class="item">
            <img src="@/assets/image/ChatDesView/phone.png" />
            <span>视频通话</span>
          </div>
          <div class="item" @click="map">
            <img src="@/assets/image/ChatDesView/place.png" />
            <span>位置</span>
          </div>
          <div class="item">
            <img src="@/assets/image/ChatDesView/redpack.png" />
            <span>红包</span>
          </div>
          <div class="item">
            <img src="@/assets/image/ChatDesView/money.png" />
            <span>转账</span>
          </div>
          <div class="item">
            <img src="@/assets/image/ChatDesView/talk.png" />
            <span>语音输入</span>
          </div>
          <div class="item">
            <img src="@/assets/image/ChatDesView/collect.png" />
            <span>我的收藏</span>
          </div>
        </div>
      </transition>
      <transition name="slide">
        <div
          style="width: 100%; height: 160px; background-color: rgb(17, 17, 17)"
          v-show="emojiShow"
        >
          <div
            style="display: inline-block"
            v-for="(item, index) in emoji"
            :key="index"
          >
            <span
              style="display: inline-block; margin: 8px 10px"
              @click="addEmogi(item)"
              >{{ item }}</span
            >
          </div>
        </div>
      </transition>
    </footer>
  </div>
</template>

<script>
import eventBus from "../eventbus.js";
import { mapActions, mapState, mapMutations } from "vuex";
import bus from "@/utils/bus.js";
export default {
  data() {
    return {
      username: "",
      imgUrl1: "",
      imgUrl2: "",
      emojiShow: false,
      userList: [],
      message: "",
      phone: "",
      show: false,
      activedKey: {
        contact: "",
        group: "",
        chatroom: "",
      },
      imgUrl: "../assets/image/ChatDesView/more.png",
      emoji: [
        "😀",
        "😃",
        "😄",
        "😁",
        "😆",
        "😅",
        "🤣",
        "😂",
        "🙂",
        "😉",
        "😊",
        "😇",
        "🥰",
        "😍",
        "🤩",
        "😘",
        "😗",
        "😚",
        "😙",
        "😚",
        "😙",
        "😋",
        "😛",
        "😜",
        "🤪",
        "😝",
        "🤗",
        "🤭",
        "🤫",
        "🤨",
        "😐",
        "😑",
        "😶",
        "🙄",
        "😬",
        "✌️",
      ],
    };
  },
  created() {
    this.$data.activedKey["contact"] = this.$route.query.phone;
    this.phone = this.$route.query.phone;
    this.message = this.$route.query.data || "";
    if (this.$route.query.data) {
      this.onSendTextMsg("txt");
    }
    return;
  },
  computed: {
    megList() {
      var that = this;
      var arr = this.$store.state.chat.msgList;
      console.log(arr, "过滤之前");
      var newArr = arr.filter(
        (item) => item.to == that.phone || item.from == that.phone
      );
      // console.log(newArr, "过滤的");
      return newArr;
    },
    ...mapState({
      userPhone: (state) => state.login.userId,
      messageCountList: (state) => state.chat.messageCountList,
    }),
  },
  mounted() {
    var that = this;
    let users = [this.userPhone, this.phone];
    WebIM.conn.fetchUserInfoById(users).then((res) => {
      // that.imgUrl1 = res.data[0].avatarurl;
      // that.imgUrl2 = res.data[2].avatarurl;
      // console.log(res.data, this.phone);
      that.imgUrl1 = res.data[that.userPhone].avatarurl;
      that.imgUrl2 = res.data[that.phone].avatarurl;
      that.username = res.data[that.phone].nickname;
      if (!that.imgUrl1) {
        that.imgUrl1 = require("@/assets/image/images/default_hd_avatar.png");
      }
      if (!that.imgUrl2) {
        that.imgUrl2 = require("@/assets/image/images/default_hd_avatar.png");
      }
      // console.log(res, "用户属性");
      // console.log(this.imgUrl);
      // console.log(that.imgUrl1);
      // console.log(that.imgUrl2);
    });
    bus.$on("imgurl", (val) => {
      that.imgUrl = val;
      that.onSendTextMsg("img");
      // console.log(111);
    });
  },
  methods: {
    ...mapActions(["onSendText", "sendImgMessage"]),
    ...mapMutations(["cleanThisCount"]),

    addEmogi(val) {
      this.message = this.message.split("");
      this.message.push(val);
      this.message = this.message.join("");
    },
    emoShow() {
      this.emojiShow = true;
      this.show = false;
    },
    back() {
      // 清除消息数量
      this.cleanCount(this.phone);
     this.$router.push({
        name: "home"
      });
    },
    handle() {
      this.show = false;
      this.emojiShow = false;
    },
    handleClick() {
      this.show = true;
      this.emojiShow = false;
    },
    takePhoto() {
      this.$router.push({
        path: "/take",
        query: {
          phone: this.phone,
        },
      });
    },
    map() {
      // this.$router.push("/map",);
      this.$router.push({
        path: "/map",
        query: {
          phone: this.phone,
        },
      });
    },
    clickFile() {
      let img = this.$refs.imgDom;
      img && img.click();
    },
    //发送消息
    onSendTextMsg(type) {
      if (type == "txt") {
        if (this.$data.message == "" || this.$data.message == "\n") {
          this.$data.message = "";
          return;
        }
        this.onSendText({
          chatType: type,
          chatId: this.$data.activedKey["contact"],
          message: this.$data.message,
        });
        this.$data.message = "";
      } else {
        this.onSendText({
          chatType: type,
          chatId: this.$data.activedKey["contact"],
          url: this.imgUrl,
        });
        this.imgUrl = "";
      }
    },
    //发送照片
    pictureChange(e) {
      console.log("111", e.target);
      let file = WebIM.utils.getFileUrl(e.target);
      console.log(file, "123", file.url);
      this.sendImgMessage({
        chatType: "img", //主要是这个参数
        chatId: this.phone, //这个位置是对方的手机号
        file: file,
        url: file.url,
      });
      this.show = false;
    },
    // 清除消息数量
    cleanCount(id) {
      if (this.userPhone) {
        if (this.messageCountList != 0) {
          this.cleanThisCount(id);
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.chatbox {
  height: 60px;
  .left {
    text-align: left;
    .img {
      float: left;
      margin: 10px 15px 0 10px;
      border-radius: 5px;
      img {
        width: 40px;
        display: block;
        border-radius: 5px;
      }
    }
    .text {
      position: relative;
      float: left;
      margin: 10px 0 0 0;
      height: 40px;
      background-color: #05c160;
      border-radius: 5px;

      &::after {
        position: absolute;
        left: -14px;
        top: 13px;
        content: "";
        height: 0;
        width: 0;
        border-width: 7px 7px;
        border-style: solid;
        border-color: transparent #05c160 transparent transparent;
        display: inline-block;
      }

      span {
        display: inline-block;
        line-height: 40px;
        margin: 0 10px;
        text-align: center;
      }
    }
  }
  .right {
    text-align: right;

    .img {
      float: right;
      margin: 10px 10px 0 10px;
      border-radius: 5px;
      img {
        width: 40px;
        border-radius: 5px;
      }
    }

    .text {
      position: relative;
      float: right;
      margin: 10px 0 0 0;
      height: 40px;
      background-color: #05c160;
      border-radius: 5px;

      &::after {
        content: "";
        position: absolute;
        right: -14px;
        top: 13px;
        height: 0;
        width: 0;
        border-width: 7px 7px;
        border-style: solid;
        border-color: transparent transparent transparent #05c160;
        display: inline-block;
      }

      span {
        display: inline-block;
        line-height: 40px;
        margin: 0 10px;
        text-align: center;
      }
    }
  }
}

