            .length {
              flex: 1;
              width: 30px;
            }
            a {
              flex: 1;
            }
          }
        }
      }
      .right {
        text-align: right;

        .img {
          float: right;
          margin: 10px 10px 0 10px;
          img {
            width: 40px;
          }
        }

        .text {
          position: relative;
          float: right;
          margin: 10px 0 0 0;
          height: 40px;
          background-color: #05c160;
          border-radius: 5px;

          &::after {
            content: "";
            position: absolute;
            right: -14px;
            top: 13px;
            height: 0;
            width: 0;
            border-width: 7px 7px;
            border-style: solid;
            border-color: transparent transparent transparent #05c160;
            display: inline-block;
          }

          span {
            display: inline-block;
            line-height: 40px;
            margin: 0 10px;
            text-align: center;
          }
        }

        .photo {
          height: 140px;
          img {
            height: 100%;
          }
        }

        .file {
          position: relative;
          min-width: 100px;
          height: 60px;
          float: right;
          border-radius: 2px;
          background-color: #05c160;
          font-size: 12px;

          span {
            width: 73px;
            height: 22px;
            overflow: hidden;
            text-overflow: ellipsis;
            position: absolute;
            top: 4px;
            left: 8px;
            border-bottom: 1px solid #999;
          }
          .bottom {
            width: 100%;
            display: flex;
            align-content: space-between;
            position: absolute;
            bottom: 4px;
            .length {
              flex: 1;
              width: 30px;
            }
            a {
              flex: 1;
            }
          }
        }
      }
    }
  }
  footer {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: center;
    .choose {
      display: flex;
      width: 100%;
      height: 45px;
      background-color: #1e1e1e;
      padding: 0 10px;

      div {
        margin: auto;
      }

      .left {
        flex: 0 0 10%;

        img {
          width: 100%;
        }
      }

      .container {
        flex: 0 0 70%;
        // border: 1px brown solid;

        input {
          width: 85%;
          height: 29px;
          overflow: hidden;
          background-color: rgba(128, 128, 128, 0.445);
          border: none;
          color: #fff;
        }
      }

      .right {
        flex: 0 0 20%;
        display: flex;
        span {
          flex: 1;

          img {
            width: 100%;
          }
        }
        button {
          margin: 0 2px;
          background-color: #05c160;
          color: #fff;
          font-size: 12px;
          border: none;
          border-radius: 5px;
        }
      }
    }

    .list {
      display: flex;
      flex-wrap: wrap;
      position: relative;
      width: 100%;
      color: #fff;
      background-color: #1e1e1e;

      .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 25%;
        height: 80px;
        // margin: auto;
        text-align: center;
        font-size: 12px;
        box-sizing: border-box;
        padding: 10px 0;

