          width: 35px;
        }
        span {
          margin-top: 10px;
        }
      }
    }
  }
  /* 其中样式相同的transition类可以进行和并 */

  .slide-enter,
  .slide-leave-to {
    transform: translateY(400px);
  }

  .slide-enter-active {
    transition: all 0.2s;
  }
  .slide-leave-active {
    transition: all 0.1s;
  }

  .slide-enter-to,
  .slide-leave {
