import vue from '@/main';
import WebIM from '../utils/WebIM';

const Friends = {
    state: {
        // 拒绝申请的用户
        refuseFriend: '',
        // 同意申请的用户
        agreeFriend: '',
        // 没找到的用户
        noFindId: '',
        // 删除好友的id
        delFriendId: '',
        // 是否有来信
        mesg: false,
        // 黑名单列表是否渲染完毕
        isRenderBlackFriends: false,
        // 所有朋友列表是否渲染完毕
        isRenderSelectFriends: false,
        // 消息条数
        count: 0,
        // 朋友的基本信息
        friendsBasicObj: {},
        // 同意申请的用户集
        friendsApplyArr: [],
        // 朋友的id
        friendsArr: [],
        // 所有朋友的基本信息
        friendsAllBasicArr: [],
        // 黑名单朋友id
        blackFriendsList: [],
        // 黑名单基本信息
        blackFriendsBasic: [],
        // 渲染列表
        renderList: []
    },
    mutations: {
        // 搜索列表的朋友信息
        searchFirendsBasic(state, payload) {
            state.friendsBasicObj = payload;
        },
        // 修改请求列表
        updateFriendsApplyArr(state, payload) {
            state.friendsApplyArr.push(payload);
        },
        // 修改朋友列表
        updateFriendsArr(state, payload) {
            state.friendsArr.push(payload);
        },
        // 修改所有朋友的基本信息列表
        searchAllFirendsBasic(state, payload) {
            state.friendsAllBasicArr.push(payload);
        },
        // 删除所有朋友的基本信息列表
        delAllFirendsBasic(state) {
            state.friendsAllBasicArr = [];
        },
        refuseFriendId(state, payload) {
            state.refuseFriend = payload;
        },
        agreeFriendId(state, payload) {
            state.agreeFriend = payload;
        },
        noFindFriendsId(state, payload) {
            state.noFindId = payload;
        },
        hasMesg(state) {
            state.mesg = true;
        },
        delMesg(state) {
            state.mesg = false;
        },
        addCount(state) {
            state.count++;
        },
        cleanCount(state) {
            state.count = 0;
        },
        // 清除朋友的申请
        cleanFriendsApplyArr(state) {
            state.friendsApplyArr = [];
        },
        // 被删除好友的id
        delFriendsId(state, payload) {
            state.delFriendId = payload;
        },
        cleanDelFriendId(state) {
            state.delFriendId = '';
        },
        getBlackFriendList(state, payload) {
            state.blackFriendsList = payload;
        },
        // 获得黑名单基本信息
        allBlackFirendsBasic(state, payload) {
            state.blackFriendsBasic.push(payload);
        },
        cleanBlackFirendsBasic(state) {
            state.blackFriendsBasic = [];
        },
        // 是否渲染完毕
        changeIsRenderBlackFriends(state) {
            state.isRenderBlackFriends = true;
        },
         // 是否渲染完毕
        changeIsRenderSelectFriends(state) {
            state.isRenderSelectFriends = true;
        },
        // 渲染列表
        renderFriendsList(state) {
            setTimeout(() => {
                state.renderList = [];
                state.renderList = state.friendsAllBasicArr;

                if (state.blackFriendsList.length !== 0) {
                    state.blackFriendsList.map((item) => {
                        state.renderList = state.renderList.filter((i) => {
                            return i.phone != item
                        });
                    });
                }
                else {
                    state.renderList = state.friendsAllBasicArr;
                }
            }, 800)
        },

    },
    actions: {
        // 通过id搜索好友
        searchFriends(context, payload) {
            // 判断是否是自己的id
            if (payload === context.rootState.login.userId) {
                context.commit('noFindFriendsId', payload);
            } else {
                WebIM.conn.getContacts().then((res) => {
                    // 判断朋友列表是否为空
                    if (res.data.length === 0) {
                        WebIM.conn.fetchUserInfoById(payload).then((res) => {

                            if (JSON.stringify(res.data[payload]) != '{}') {
                                let friendsBasic = JSON.parse(res.data[payload].ext);
                                let basicObj = {
                                    nickname: res.data[payload].nickname,
                                    avatarurl: res.data[payload].avatarurl,
                                    phone: res.data[payload].phone,
                                    address: friendsBasic.address,
                                    isFriends: false
                                }
                                context.commit('searchFirendsBasic', basicObj);
                            }
                            else {
                                context.commit('noFindFriendsId', payload);
                            }

                        }).catch(error => {
                            console.log(error);
                            context.commit('noFindFriendsId', payload);
                        })
                    }
                    else {
                        let arr = res.data;
                        let basicObj = {};
                        let flag = false;

                        arr.map((item) => {
                            if (item == payload) {
                                flag = true
                            }
                        })

                        WebIM.conn.fetchUserInfoById(payload).then((res) => {
                            if (JSON.stringify(res.data[payload]) !== '{}') {
                                let friendsBasic = JSON.parse(res.data[payload].ext);
                                basicObj = {
                                    nickname: res.data[payload].nickname,
                                    avatarurl: res.data[payload].avatarurl,
                                    phone: res.data[payload].phone,
                                    address: friendsBasic.address,
                                    isFriends: true
                                }
                                if (flag) {
                                    context.commit('searchFirendsBasic', basicObj);
                                } else {
                                    basicObj.isFriends = false
                                    context.commit('searchFirendsBasic', basicObj);
                                }
                            } else {
                                context.commit('noFindFriendsId', payload);
                            }
                        }).catch(error => {
                            console.log(error);
                            context.commit('noFindFriendsId', payload);
                        })
                    }
                })
            }
        },
        // 发送申请信息给朋友
        sendMsgToFriends(context, { id, text }) {
            WebIM.conn.addContact(id, text);
        },
        // 更改申请列表信息
        updateFriendsApply(context, { id, text }) {
            WebIM.conn.fetchUserInfoById(id).then((res) => {
                context.commit('updateFriendsApplyArr', {
                    id,
                    text,
                    nickname: res.data[id].nickname
                });
            })
        },
        // 同意请求
        agreeApplyFromFriends(context, payload) {
            WebIM.conn.acceptContactInvite(payload);
        },
        // 拒绝请求
        refuseApplyFromFriends(context, payload) {
            WebIM.conn.declineContactInvite(payload);
        },
        // 修改通讯录列表
        updateFriendsList(context, payload) {
            context.commit('updateFriendsArr', payload)
        },
        // 搜索所有的朋友
        searchAllFriends(context) {
            if (context.rootState.user.userId !== '') {
                WebIM.conn.getContacts().then((res) => {

                    res.data.forEach((item, i) => {
                        WebIM.conn.fetchUserInfoById(item).then((res) => {
                            let friendsBasic = JSON.parse(res.data[item].ext);
                            let basicObj = {
                                nickname: res.data[item].nickname,
                                avatarurl: res.data[item].avatarurl,
                                address: friendsBasic.address,
                                phone: res.data[item].phone,
                            }
                            context.commit('searchAllFirendsBasic', basicObj);
                        }).catch(error => {
                            console.log(error);
                        });
                        if (i == res.data.length - 1) {
                            context.commit('changeIsRenderSelectFriends');
                        }
                    })
                })
            }
        },
        delAllFirendBasic(context) {
            context.commit('delAllFirendsBasic');
        },
        // 拒绝申请的用户的id
        refuseFriendsList(context, payload) {
            context.commit('refuseFriendId', payload);
        },
        // 同意申请的用户的id
        agreeFriendsList(context, payload) {
            context.commit('agreeFriendId', payload);
        },
        delFridends(context, payload) {
            WebIM.conn.deleteContact(payload);
        },
        delFriendsList(context, payload) {
            if (payload !== context.rootState.login.userId) {
                context.commit('delFriendsId', payload);
            }
        },
        // 添加黑名单
        addBlackFriends(context, payload) {
            WebIM.conn.addUsersToBlocklist({
                //可以添加单个用户 ID 或批量添加多个用户 ID 组成的数组。
                name: payload,
            })
        },
        // 获得黑名单列表
        // 获取黑名单基本信息
        getBlackFriendsList(context) {
            if (context.rootState.user.userId !== '') {

                WebIM.conn.getBlocklist().then((res) => {

                    context.commit('cleanBlackFirendsBasic');

                    if (res.data.length !== 0) {
                        res.data.forEach((item, i) => {

                            WebIM.conn.fetchUserInfoById(item).then((res) => {

                                let basicObj = {
                                    nickname: res.data[item].nickname,
                                    avatarurl: res.data[item].avatarurl,
                                    phone: res.data[item].phone,
                                    isBlack: true
                                }

                                context.commit('allBlackFirendsBasic', basicObj);
                            }).catch(error => {
                                console.log(error);
                            });
                            if (i == res.data.length - 1) {
                                context.commit('changeIsRenderBlackFriends');
                            }
                        })
                    }
                    else {
                        context.commit('changeIsRenderBlackFriends');
                    }
                    context.commit('getBlackFriendList', res.data);
                });
            }
        },

        // 清除黑名单信息
        cancelBlackFriends(context, payload) {
            WebIM.conn.removeUserFromBlocklist({ name: payload });
        },
    },
}

export default Friends;