<template>
  <div class="my">
    <div class="header" @click="routerUserInfo">
      <div class="user-log">
        <img :src="imgurl" />
      </div>
      <div class="user-dec">
        <h2>{{ userNickName }}</h2>
        <div class="user-id">
          <span>{{ userPhone }}</span>
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
    </div>
    <div class="status">
      <span>+状态</span>
      <span>等5个朋友</span>
    </div>

    <div class="one-cell">
      <i><img src="../../assets/image/discover-img/my1.png" alt="" /></i>
      <div class="text" @click="$router.push('/check')"> 
        <p>服务</p>
        <van-icon name="arrow" color="rgb(107,107,107)" />
      </div>
    </div>

    <div class="five-cell">
      <div class="cell">
        <i><img src="../../assets/image/discover-img/my2.png" alt="" /></i>
        <div class="text">
          <p>收藏</p>
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="cell">
        <i><img src="../../assets/image/discover-img/my3.png" alt="" /></i>
        <div class="text">
          <p>朋友圈</p>
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="cell">
        <i><img src="../../assets/image/discover-img/my4.png" alt="" /></i>
        <div class="text">
          <p>视频号</p>
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="cell">
        <i><img src="../../assets/image/discover-img/my5.png" alt="" /></i>
        <div class="text">
          <p>卡包</p>
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
      <div class="cell">
        <i><img src="../../assets/image/discover-img/my6.png" alt="" /></i>
        <div class="text">
          <p>表情</p>
          <van-icon name="arrow" color="rgb(107,107,107)" />
        </div>
      </div>
    </div>
    <!-- 设置 -->
    <div class="one-cell" @click="routerSetup">
      <i><img src="../../assets/image/discover-img/my7.png" alt="" /></i>
      <div class="text">
        <p>设置</p>
        <van-icon name="arrow" color="rgb(107,107,107)" />
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import WebIM from "../../utils/WebIM";
export default {
  data() {
    return {
      imgurl: require("../../assets/image/images/default_hd_avatar.png"),
      userNickName: "用户",
    };
  },
  computed: {
    ...mapState({
      userPhone: (state) => state.login.userId,
      userPic: (state) => state.user.userPic,
      nickName: (state) => state.user.nickName,
    }),
  },
  created() {
    if (this.userPic != "") {
      this.imgurl = this.userPic;
    }
    if (this.nickName != "") {
      this.userNickName = this.nickName;
    }
  },
  watch: {
    userPic(newV) {
      this.imgurl = newV;
    },
    nickName(newV) {
      this.userNickName = newV;
    },
  },
  methods: {
    routerSetup() {
      this.$router.push({
        name: "setUp",
        params: {
          imgurl: this.imgurl,
          userNickName: this.userNickName,
          userPhone: this.userPhone,
        },
      });
    },
    routerUserInfo() {
      this.$router.push({
        name: "userInfo",
        params: {
          imgurl: this.imgurl,
          userNickName: this.userNickName,
          userPhone: this.userPhone,
        },
      });
    },
  },
};
</script>
