      line-height: 34px;
      height: 34px;
      padding: 2px;

      img {
        width: 100%;
      }
    }
    .text {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      p {
        //   line-height: 44px;
        margin-left: 10px;
      }
      .van-icon-arrow {
        padding: 0;
      }
    }
  }

  .five-cell {
    margin-top: 10px;
    .cell {
      display: flex;
