<template>
  <div class="black-friends">
    <nav>
      <van-row>
        <van-col span="4" @click="backFunc">&lt;</van-col>
        <van-col span="14" offset="1">通讯录黑名单</van-col>
      </van-row>
    </nav>
    <div class="black-list">
      <div
        class="black-item"
        v-for="l in blackFriendsBasic"
        :key="l.phone"
        @click="goToWantToChat(l)"
      >
        <div class="img">
          <img :src="l.avatarurl" alt="img" />
        </div>
        <div class="desc">{{ l.nickname || `用户${l.phone}` }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  created() {
    this.getBlackFriendsList();
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.userId,
      isAddBlackFriend: (state) => state.friends.isAddBlackFriend,
      blackFriendsBasic: (state) => state.friends.blackFriendsBasic,
    }),
  },
  watch: {
    userId() {
      this.getBlackFriendsList();
    },
  },
  methods: {
    ...mapActions(["getBlackFriendsList"]),
    backFunc() {
      this.$router.push({
        name: "friendsLimit",
      });
    },
    goToWantToChat(obj) {
      this.$router.push({
        name: "wantToChat",
        params: { list: JSON.stringify(obj) },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.black-friends {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background-color: rgb(17, 17, 17);
  color: white;

  nav {
    width: 100vw;
    height: 45px;
    background-color: rgb(17, 17, 17);
    line-height: 45px;
    color: white;
    text-align: center;
    font-size: 14px;
  }

  .black-list {
    .black-item {
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 50px;
      background-color: rgb(24, 24, 24);
      padding: 0 0 0 15px;

      .img {
        width: 40px;
        height: 40px;
        margin-right: 15px;

        img {
          width: 100%;
          height: 100%;
          display: block;
          border-radius: 5px;
        }
      }

      .desc {
        width: 85%;
        height: 100%;
        line-height: 50px;
        border-bottom: 1px solid rgba(204, 203, 203, 0.1);
      }
    }
  }
}
</style>