
import {  Switch ,Checkbox, CheckboxGroup,Area, RadioGroup, Radio, Toast, Badge, Popup, Icon, SwitchCell, Form, Field, Uploader, Search, Col, Row, Tabbar, TabbarItem, Popover, Cell, CellGroup, Button, ActionSheet, PullRefresh, NavBar, ShareSheet } from 'vant'

const UIComponents = [
    Switch  ,Checkbox, CheckboxGroup,Area, RadioGroup, Radio, Toast, Badge, Popup, Icon, SwitchCell, Form, Field, Uploader, Search, Col, Row, Tabbar, TabbarItem, Popover, Cell, CellGroup, Button, ActionSheet, PullRefresh, NavBar, ShareSheet
]

const VantUIPlugin = {};

VantUIPlugin.install = function (Vue) {

    UIComponents.forEach(Component => {

        Vue.use(Component)

    })

}

export default VantUIPlugin